@import url(https://fonts.googleapis.com/css?family=Exo:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i&display=swap&subset=latin-ext);
body {
  font-family: 'Exo', sans-serif;
}

#root{ width: 100%;}

.appWeatherDatas{ display: flex; align-items: center; justify-content: center; flex-direction: row}
.weatherItems{ background:#fff; padding: 20px; border-radius:6px; margin: 0 10px; width: 20%; box-shadow: 0 1px 1px rgba(0,0,0,.2);}
.tempInfo{ text-align: left; margin: 10px 0 0;}
.tempInfo span{ display: block; margin: 0 0 5px; font-size: 14px;}
.extraInfo{ text-align: left;}
.extraInfo span{ display: block; margin: 0 0 5px; font-size: 14px;}
.weatherIcon{ display: block; height: auto; width: 100%; display: flex; flex-direction: row; justify-content: space-between; align-items: center}
.cityTitle{ display: block; font-size: 26px; font-weight: 600; text-align: left; }
.tempData{font-size: 26px; font-weight: 300;}
.btn30Days{ margin: 20px 0 0; font-size:12px;}
.btn30Days button{ background: #ccc; border-radius: 6px; color: #fff; padding: 5px 10px; }
.btn30Days button:hover{ opacity: 0.8;}
.table td, .table th{ vertical-align: middle;}
.table tr:nth-child(odd){ background: #f1f1f1;}
.table td{ padding: 5px;}
.table{ margin: 20px 0 0;}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

